import React, {useLayoutEffect} from "react";
import {Canvas} from "@react-three/fiber";
import {OrbitControls} from "@react-three/drei";
import anime from "animejs";
import Kalmars3D from "./Kalmars3D";
import Lights from "../../components/Lights";
import {useTranslation} from "react-i18next";

const HeroBlock = ({nextPage}) => {
    const {t} = useTranslation();

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#we-are-content',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])
    return (
        <div id="we-are" className="container mx-auto">
            <div className="grid lg:grid-cols-12 pt-32 md:pt-0 md:min-h-screen">
                <div id="we-are-content" className="col-span-6 flex justify-center items-center  flex-col py-12 lg:py-0">
                    <div className="flex flex-col justify-center relative text-center sm:text-left w-full sm:w-3/4 lg:w-auto">
                        <div className="css--we-are-header text-center" style={{zIndex: 2}}>{t('about_us')}</div>
                        {/*<div className="css--we-are-header-second" style={{zIndex: 2}}>cyborgs</div>*/}
                    </div>
                    <div className="w-4/4 mt-10 text-center text-white md:text-lg text-xs">We are a software development company and we have built multiple digital products for our customers and for ourselves. We have experience in developing tailored solutions to meet client requirements in a short amount of time. Moreover, we would like to point out that we have done it by using various technologies and approaches which shows our flexibility and skill set.
                    </div>
                </div>
                <div className="col-span-6 h-screen hidden lg:block css--we-are-hero">
                    <div style={{width: "100%", height: "100%", position: "relative"}}>
                        <Canvas
                            gl={{alpha: true}}
                            camera={{position: [0, 0, 20], fov: 40}}>
                            <Lights/>
                            <OrbitControls makeDefault autoRotate autoRotateSpeed={0.3} enableZoom={false} zoomSpeed={0.1}/>
                            <React.Suspense fallback={null}>
                                <Kalmars3D/>
                            </React.Suspense>
                        </Canvas>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default HeroBlock;
