import React from "react";
import {useTranslation} from "react-i18next";

const WeAreDoing = ({nextPage}) => {
    const {t} = useTranslation();

    return (
        <div className="pt-32 md:pt-0 md:min-h-screen flex items-center">
                <div className="container mx-auto" style={{zIndex: '10'}}>
                    <div className="grid grid-cols-1 gap-4 flex justify-center">
                        <div className="flex justify-center">
                            <div className="css--we-are-header text-center mb-8">{t('field_of_expertise')}</div>
                        </div>
                    </div>
                    <div className="grid grid-cols-1 md:gap-4 pt-20">
                        <div className="flex justify-center">
                            <div className="text-2xl text-white font-bold md:text-4xl">
                                {t('we_are_doing')}
                            </div>
                        </div>
                    </div>
                    <div className="grid grid-cols-1">
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">01</span><span className="text-white text-xl md:text-3xl">{t('e-commerce_dev')}</span></div>
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">02</span><span className="text-white text-xl md:text-3xl">{t('saas_dev')}</span></div>
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">03</span><span className="text-white text-xl md:text-3xl">{t('iot_and_custom')}</span></div>
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">04</span><span className="text-white text-xl md:text-3xl">{t('mobile_apps')}</span></div>
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">05</span><span className="text-white text-xl md:text-3xl">{t('websites_dev')}</span></div>
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">06</span><span className="text-white text-xl md:text-3xl">{t('api_dev')}</span></div>
                        <div className="text-secondary mt-6 flex"><span className="my-auto css--connecting-line">07</span><span className="text-white text-xl md:text-3xl">{t('any_other')}</span></div>
                    </div>
                </div>
        </div>
    )
}
export default WeAreDoing;


