import React, {useLayoutEffect} from "react";
import anime from "animejs";
import HeroBlock from "./Hero";
import WeAreDoing from "./WeAreDoing";
import Technologies from "./Technologies";
import {useTranslation} from "react-i18next";

const WeAreBlock = ({nextPage}) => {
    const {t} = useTranslation();

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#we-are-content',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 800,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])
    return (
       <>
           <div className='absolute' style={{bottom: '5%', right: '7%', zIndex: '999'}}>
           </div>
           <div className="css--weare-lines-parent md:grid hidden">


               <div className="css--weare-line-1">
                   <div className="css--weare-div">
                       <div className="css--weare-text-div bg-white text-black">{t('what_we_do')}</div>
                   </div>
               </div>
               <div className="css--weare-line-2 flex justify-end">
                   <div className="css--weare-div-2nd">
                       <div className="css--weare-text-div-2nd bg-white text-black">{t('technologies')}</div>
                   </div>
               </div>
           </div>
           <HeroBlock/>
           <WeAreDoing/>
           <Technologies/>

       </>
    )
}
export default WeAreBlock;
