import React, {useState} from 'react';
import SwiperCore, {Virtual} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/components/autoplay';
import 'swiper/swiper-bundle.css';
import {useTranslation} from "react-i18next";

const Technologies = ({nextPage}) => {
    const {t} = useTranslation();

    const [swiperRef, setSwiperRef] = useState(null);

    const slideTo = (index) => {
        swiperRef.slideTo(index - 1, 0);
    };

    return (
        <div id="kyc" className="flex justify-center content-center pt-32 md:pt-0 md:min-h-screen">
            <div className="flex justify-center items-center flex-wrap overflow-hidden">
                <div className="container px-0 md:px-28" style={{zIndex: '10'}}>
                    <div className="grid grid-cols-1 gap-4 flex justify-center">
                        <div className="text-white text-center font-bold text-2xl md:text-4xl">
                            {t('technologies')}
                        </div>
                    </div>
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4 flex justify-center py-20">
                        <div className="flex justify-center">
                            <button onClick={() => slideTo(1)} className="btn-0-p btn w-full ">
                                FRONT-END
                            </button>
                        </div>
                        <div className="flex justify-center">

                            <button onClick={() => slideTo(2)} className="btn w-full btn-0-p">
                                BACK-END
                            </button>
                        </div>
                        <div className="flex justify-center">
                            <button onClick={() => slideTo(3)} className="btn w-full btn-0-p">
                                DATABASES
                            </button>
                        </div>
                        <div className="flex justify-center">
                            <button onClick={() => slideTo(4)} className="btn w-full btn-0-p">
                                MOBILE
                            </button>
                        </div>
                    </div>
                    <Swiper slidesPerView={1} onSwiper={setSwiperRef}
                            className="mySwiper">
                        <SwiperSlide>
                            <div className="grid grid-cols-2 md:grid-cols-5">
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/vue.svg" className="h-full w-auto" alt="VueJs"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Vue
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/react.svg" className="h-full w-auto" alt="React"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        React
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/threejs.svg" className="h-full w-auto" alt="ThreeJs"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        ThreeJS
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/tailwind.svg" className="h-full w-auto" alt="Tailwind"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Tailwind
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/bootstrap.svg" className="h-full w-auto" alt="Bootstrap"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Bootstrap
                                    </div>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="grid grid-cols-2 md:grid-cols-6">
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/laravel.svg" className="h-full w-auto" alt="Laravel"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Laravel
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/wordpress.svg" className="h-full w-auto" alt="Wordpress"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Wordpress
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/shopify.svg" className="h-full w-auto" alt="Shopify"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Shopify
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/gatsby.svg" className="h-full w-auto" alt="GatsbyJS"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        GatsbyJS
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/nodejs.svg" className="h-full w-auto" alt="NodeJs"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        NodeJS
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/python.svg" className="h-full w-auto" alt="Python"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Python
                                    </div>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="grid grid-cols-2 md:grid-cols-5">
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/mysql.svg" className="h-full w-auto" alt="MySQL"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        MySQL
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/postgre.svg" className="h-full w-auto" alt="PostgresSQL"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        PostgresSQL
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/mongo.svg" className="h-full w-auto" alt="MongoDB"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        MongoDB
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/mariadb.svg" className="h-full w-auto" alt="MariaDB"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        MariaDB
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/sqlite.svg" className="h-full w-auto" alt="SQLite"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        SQLite
                                    </div>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="grid grid-cols-2 md:grid-cols-4">
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo">
                                        <img src="img/develop_stack/expo.svg" className="h-full w-auto" alt="Expo"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Expo
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/react.svg" className="h-full w-auto" alt="React Native"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        React Native
                                    </div>
                                </div>
                                <div className="grid justify-center">
                                    <div className="css--technologies-logo flex justify-center">
                                        <img src="img/develop_stack/figma.svg" className="h-full w-auto" alt="Figma"/>
                                    </div>
                                    <div className="text-white text-xl flex justify-center">
                                        Figma
                                    </div>
                                </div>
                            </div>
                        </SwiperSlide>
                    </Swiper>
                </div>
            </div>
        </div>
    )
}
export default Technologies;


